import React, { Component } from 'react';
import jetpack from 'fs-jetpack'

class Items extends Component {
  constructor(props) {
    super(props);
    this.state = {
      feed: []
    }
  }

  componentDidMount() {
    const datadir = jetpack.cwd("/Users/vincenzo.pirrone/dev/1inbox/data");
    this.setState({ feed: datadir.list().forEach(jetpack.read) });
  }

  render() {
    const feed = this.state.feed.map((item) =>
      <div class="item">
        <div class="item-content">{item}</div>
      </div>
    );
    return (
      <div>{feed}</div>
    );
  }
}

export default Items;


