import React, { Component } from 'react';
import SimpleMDE from 'react-simplemde-editor';
import "simplemde/dist/simplemde.min.css";


class Popup extends Component {

  constructor(props) {
    super(props);

    this.state = {
      value: localStorage.getItem(`smde_${this.props.id}`) || this.props.value
    };
  }

  updateCode(md) {
    this.setState({
      markdownSrc: md
    });
  }

  saveAndExit(e) {
    this.props.closePopup(this.state.markdownSrc);
  }

  exit(e) {
    this.props.closePopup(null);
  }

  render() {
    const id = this.props.id;
    return (
      <div className='popup'>
        <div className='popup-inner'>
          <div className="editor-pane">
            <SimpleMDE
              id={id}
              value={this.state.value}
              // onChange={this.updateCode.bind(this)}
              options={{
                autosave: {
                  enabled: true,
                  uniqueId: id,
                  delay: 1000
                },
                autofocus: true,  
                spellChecker: false,
                toolbar: false,
                status: false
              }}
            />
          </div>
          <button onClick={this.saveAndExit.bind(this)}>Done</button>
          <button onClick={this.exit.bind(this)}>Cancel</button>
        </div>
      </div>
    );
  }
}

export default Popup;


